import React from "react"
import {
  bootstrapImg,
  cssImg,
  htmlImg,
  mysqlImg,
  phpImg,
  vueImg,
} from "../components/Home/Icons"
import LayoutProyecto from "../components/Layout/Proyecto"
import { images } from "../components/Pagos/Imagenes"

const pagos = () => {
  const icons = [
    { src: phpImg, alt: "php" },
    { src: mysqlImg, alt: "mysql" },
    { src: vueImg, alt: "vue" },
    { src: htmlImg, alt: "html" },
    { src: cssImg, alt: "css" },
    { src: bootstrapImg, alt: "bootstrap" },
  ]
  return (
    <LayoutProyecto
      title="Administrador de pagos"
      subtitle="Sistema que gestiona pagos de alumnos"
      description="Imágenes ilustrativas del sistema que se creo para gestionar pagos de los alumnos para la universidad filadelfia de México el cual cuenta roles de usuario, administrador de ordenes de pago, pagos pendientes, pagos realizados, de alumnos, bancos, carreras, usuarios, tipos de pago, bitácora, respaldo de ciertos módulos en formato excel y respaldo de sql, de base de datos"
      icons={icons}
      images={images}
    />
  )
}

export default pagos
